import { yupResolver } from '@hookform/resolvers/yup'
import VisibilityIcon from '@mui/icons-material/Visibility'
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff'
import {
	Autocomplete,
	FormControl,
	IconButton,
	InputAdornment,
	MenuItem,
	Select,
	Stack,
	TextField,
	Typography,
} from '@mui/material'
import { ACCOUNT_API } from 'API/account'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'

interface FormInput {
	type: 'Default' | 'MSAL'
	name: string
	emailName: string
	emailDomain: string
	password: string
	rePassword: string
}
export default function CreateAccount() {
	let { id } = useParams<{ id: string }>()
	const nav = useNavigate()
	const dispatch = useAppDispatch()
	const allowDomains = useAppSelector((s) => s.UI.allowDomains)
	const passwordRole = useAppSelector((s) => s.UI.config).passwordRole
	const [loading, setLoading] = useState(false)
	const [passwordType, setPasswordType] = useState('password')
	const schema = yup.object({
		name: yup.string().when('type', {
			is: 'Default',
			then: yup.string().required('Must enter name'),
		}),
		emailName: yup.string().required(),
		emailDomain: yup.string().required(),
		password: yup.string(),
		rePassword: yup.string().oneOf([yup.ref('password'), null], 'Passwords must match'),
	})
	const {
		register,
		handleSubmit,
		setValue,
		getValues,
		watch,
		setError,
		clearErrors,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			type: 'MSAL',
			password: '',
			rePassword: '',
			emailDomain: allowDomains[0].name,
		},
	})
	useEffect(() => {
		const subscription = watch((value, { name, type }) => {
			if (name === 'password' && value.password) {
				const rex = new RegExp(passwordRole || '[\\s\\S]+')
				if (!rex.test(value.password)) {
					setError('password', {
						message: 'パスワードポリシーと一致しません',
					})
				} else {
					clearErrors('password')
				}
			}
		})
		return () => subscription.unsubscribe()
	}, [clearErrors, passwordRole, setError, watch])

	useEffect(() => {
		const subscription = watch((value, { name }) => {
			if (name === 'rePassword') {
				const password = getValues('password')
				const rePassword = getValues('rePassword')

				if (password !== rePassword) {
					setError('rePassword', {
						message: 'パスワードが一致しません',
					})
				} else {
					clearErrors('rePassword')
				}
			}
		})

		return () => subscription.unsubscribe()
	}, [clearErrors, setError, watch, getValues])

	if (!id) return null
	const onSubmit: SubmitHandler<FormInput> = async ({ type, name, emailName, emailDomain, password }) => {
		try {
			setLoading(true)
			const { error, message } = await ACCOUNT_API.create({
				email: emailName + '@' + emailDomain,
				groupId: Number(id),
				name: type === 'MSAL' ? emailName : name,
				type,
				password,
			})
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			setTimeout(() => {
				nav(`/group/${id}`)
			}, 300)
		} finally {
			setLoading(false)
		}
	}
	return (
		<DialogConfirm
			open
			loading={loading}
			handleClose={() => nav(`/group/${id}`)}
			title="アカウント追加"
			handleSubmit={handleSubmit(onSubmit)}
			primaryButtonText="作成"
			component="form"
			maxWidth="md"
		>
			<Stack gap={2}>
				<Autocomplete
					disablePortal
					options={[
						{ label: 'Office365アカウント', value: 'MSAL' },
						{ label: 'メールアドレスとパスワード', value: 'Default' },
					]}
					getOptionLabel={(option) => option.label}
					fullWidth
					value={
						getValues('type') === 'Default'
							? { label: 'メールアドレスとパスワード', value: 'Default' }
							: { label: 'Office365アカウント', value: 'MSAL' }
					}
					renderInput={(params) => (
						<TextField {...params} label={'アカウントタイプ'} required variant="standard" error={'type' in errors} />
					)}
					onChange={(_event, selectedOption) => {
						if (selectedOption) {
							setValue('type', selectedOption.value as 'Default' | 'MSAL', {
								shouldValidate: true,
							})
						}
					}}
				/>

				<Stack direction="row" alignItems="flex-end" gap={0.5}>
					<TextField
						variant="standard"
						required
						fullWidth
						label={'メールアドレス'}
						placeholder="xxxx"
						InputLabelProps={{ shrink: true }}
						{...register('emailName')}
						error={'emailName' in errors}
					/>
					<FormControl sx={{ width: '100%' }} size="small">
						<Select
							value={getValues('emailDomain')}
							onChange={(e) => {
								setValue('emailDomain', e.target.value, {
									shouldValidate: true,
								})
							}}
						>
							{allowDomains.map((v) => {
								return (
									<MenuItem key={v.id} value={v.name}>
										@{v.name}
									</MenuItem>
								)
							})}
						</Select>
					</FormControl>
				</Stack>
				{getValues('type') === 'Default' && (
					<>
						<TextField
							variant="standard"
							required
							margin="dense"
							fullWidth
							size="small"
							label={'名前'}
							placeholder="佐藤 太郎"
							InputLabelProps={{ shrink: true }}
							{...register('name')}
							error={'name' in errors}
						/>
						<TextField
							variant="outlined"
							required
							margin="dense"
							fullWidth
							size="small"
							label="パスワード"
							{...register('password')}
							error={'password' in errors}
							helperText={errors.password?.message}
							type={passwordType}
							InputProps={{
								endAdornment: (
									<InputAdornment position="end">
										{passwordType === 'password' ? (
											<IconButton onClick={() => setPasswordType('text')} edge="end">
												<VisibilityIcon />
											</IconButton>
										) : (
											<IconButton onClick={() => setPasswordType('password')} edge="end">
												<VisibilityOffIcon />
											</IconButton>
										)}
									</InputAdornment>
								),
							}}
						/>
						<Stack alignItems={'center'}>
							<TextField
								variant="outlined"
								required
								margin="dense"
								fullWidth
								size="small"
								label="パスワード再入力"
								{...register('rePassword')}
								error={'rePassword' in errors}
								helperText={errors.rePassword?.message}
								type={passwordType}
								onPaste={(e) => {
									e.preventDefault()
								}}
								InputProps={{
									endAdornment: (
										<InputAdornment position="end">
											{passwordType === 'password' ? (
												<IconButton onClick={() => setPasswordType('text')} edge="end">
													<VisibilityIcon />
												</IconButton>
											) : (
												<IconButton onClick={() => setPasswordType('password')} edge="end">
													<VisibilityOffIcon />
												</IconButton>
											)}
										</InputAdornment>
									),
								}}
							/>
							<Typography variant="caption" component="div" gutterBottom>
								パスワードの再入力時にコピーアンドペーストは使用できません
							</Typography>
						</Stack>
					</>
				)}
			</Stack>
		</DialogConfirm>
	)
}
